.Portfolio {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 80px 88vh 150vh 110vh 30vh 115vh 257px;
    /*grid-template-rows: 80px 400px 500px 550px 204px 500px 257px;*/
    grid-template-areas: "header" "myResume" "mySkills"
    "myJobs" "myFreelance" "myContacts" "myFooter";
    background-color: darkseagreen;
}

@media screen and (max-width: 1150px) {
    .Portfolio {
        grid-template-rows: 80px 88vh 150vh 110vh 30vh 115vh 257px;
    }
}

@media screen and (max-width: 1140px) {
    .Portfolio {
        grid-template-rows: 80px 88vh 150vh 175vh 30vh 115vh 257px;
    }
}

